import React from 'react';
import { Box, Divider, Typography } from '@mui/material';
import { FormattedMessage } from 'react-intl';
import AIExampleVideo from 'assets/video/aiPromotion/ai-video-ex.mp4';
import ExampleImg from 'assets/images/aiPromotion/img-ex.png';

// 컴포넌트 네이밍 룰 "AIL" prefix 를 붙여주세요.
// ex) AILVideoContainer
import { AILVideoContainer, AILExampleContainer } from './styles';

/**
 * ExampleView 페이지
 *
 * 각 컴포너트별 예시
 */
const ExampleView = () => {
  return (
    <AILExampleContainer>
      <Typography variant="h1">AI Page</Typography>
      <Typography variant="body1" sx={{ margin: '20px 0 0 20px' }}>
        - /src/view/example/index.tsx
      </Typography>
      <Divider sx={{ width: '100%' }} />
      <Typography variant="h5">컴포넌트 네이밍 룰</Typography>
      <br />
      <Typography variant="h3">
        컴포넌트 네이밍 룰 AIL prefix 를 붙여주세요
        <br />
        ex) AILVideoContainer
      </Typography>
      <Divider sx={{ width: '100%' }} />
      <Typography variant="h5">다국어 언어 설정 Example</Typography>
      <br />
      <Typography variant="h3">
        <FormattedMessage id="MSG_SAMPLE_RESOURCE" />
      </Typography>
      <Divider sx={{ width: '100%' }} />
      <Typography variant="h5">이미지 사용 설정 Example</Typography>
      <br />
      이미지는 확장자 상관없이 최적화된 이미지를 사용
      <br />
      <Box component="figure">
        <img src={ExampleImg} alt="" />
      </Box>
      <Divider sx={{ width: '100%' }} />
      <Typography variant="h5">미디어 사용 설정 Example</Typography>
      <br />
      <AILVideoContainer>
        <video width="100%" muted autoPlay loop playsInline>
          <source src={AIExampleVideo} type="video/mp4" />
        </video>
      </AILVideoContainer>
    </AILExampleContainer>
  );
};

export default ExampleView;
